import { LoadingProvider } from './../../providers/loading/loading';
import { GlobalProvider } from './../../providers/global/global';
import { Component, Input } from '@angular/core';
import { NavController, NavParams, AlertController, App, Tabs, IonicPage } from 'ionic-angular';
import { AuthProvider } from '../../providers/auth/auth';

/**
 * Generated class for the RegisterPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-register',
  templateUrl: 'register.html',
})
export class RegisterPage {
  @Input() data: any;
  @Input() events: any;

  public name: string;
  public email: string;
  public password: string;
  public password_confirmation:string;
  public user_type: string;

  public errorNameEmpty: boolean;
  public errorEmailEmpty: boolean;
  public errorEmailInvalid: boolean;
  public errorPasswordEmpty: boolean;
  public errorPasswordConfirmEmpty: boolean;
  public errorPasswordConfirmInvalid: boolean;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams, 
    public global: GlobalProvider, 
    public alertCtrl: AlertController,
    public app: App,
    public auth: AuthProvider,
    public loading: LoadingProvider
  ) {
    
    this.errorNameEmpty= false;
    this.errorEmailEmpty= false;
    this.errorEmailInvalid = false;
    this.errorPasswordEmpty = false;
    this.errorPasswordConfirmEmpty = false;
    this.errorPasswordConfirmInvalid = false;

    this.user_type = 'distributor';

    this.events =  {
      "onRegister" : this.onRegister
    }

  }

  onEvent = (event: string): void => {
    if (event == "onRegister" && !this.validate()) {
        return ;
    }
    if (this.events[event]) {
        this.events[event]({
            'email': this.email,
            'password': this.password
        });
    }        
  }

  validate():boolean {
    this.errorNameEmpty = false;
    this.errorEmailEmpty= false;
    this.errorEmailInvalid = false;
    this.errorPasswordEmpty = false;
    this.errorPasswordConfirmEmpty = false;
    this.errorPasswordConfirmInvalid = false;
    let re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    if (!this.name ||this.name.length == 0) {
      this.errorNameEmpty = true;
  }
    if (!this.email ||this.email.length == 0) {
        this.errorEmailEmpty = true;
    }
    else if (!re.test(String(this.email).toLowerCase())) {
      this.errorEmailInvalid = true;
    }

    if (!this.password || this.password.length == 0) {
        this.errorPasswordEmpty = true;
    }

    if (!this.password_confirmation || this.password_confirmation.length == 0) {
      this.errorPasswordConfirmEmpty = true;
    }
    else if (this.password !== this.password_confirmation) {
      this.errorPasswordConfirmInvalid = true;
    }
    
    return !this.errorNameEmpty && !this.errorEmailEmpty && !this.errorEmailInvalid && !this.errorPasswordEmpty && !this.errorPasswordConfirmEmpty && !this.errorPasswordConfirmInvalid;
  }

  // login and go to home page
  onRegister = (params): void => {
    this.loading.show();

    this.auth.signup({name: this.name, email: this.email, password: this.password, password_confirmation: this.password_confirmation, user_type: this.user_type}).then(
      (data:any) => {
        console.log(data.success);
        this.loading.hide();

        let alert = this.alertCtrl.create({
          title: 'Đã gửi bản đăng ký',
          subTitle: 'Chúng tôi sẽ liên hệ xác nhận trong thời gian sớm nhất!',
          buttons: [
            {
              text: 'OK',
              role: 'ok',
              handler: () => {
                this.global.isAuthenticated = true;
                const tabsNav = this.app.getNavByIdOrName('mainTabs') as Tabs;
                tabsNav.select(0);
              }
            }
          ]
        });
        alert.present();
          
      },
      (err) => {
        console.log(err.error.message);
        this.loading.hide();
        
        let message = "Không thể kết nối tới máy chủ!";
        if (err.error.message == 'email_used'){
          message = "Email đã được sử dụng"
        }
        let alert = this.alertCtrl.create({
          title: 'Lỗi',
          message: message,
          buttons: [
            {
              text: 'OK',
              role: 'ok',
            }
          ]
        });
        alert.present();
      }
    );

  }

  openLoginPage() {
    this.navCtrl.setRoot('LoginPage');
  }

}



